$primary-color: #f04e29;
$secondary-color: #0097c1;
$tertiary-color: #e5a812;
$white-color: #ffffff;
$grey-color: #dcddde;

html{
    font-size: 14px;
    font-family: Arial, Helvetica, sans-serif;
}
body{
    padding: 0;
    margin: 0;
}

.brand {
    width: 100%;
    text-align: center;
    padding-top: 10px;
}
.navWide {
    display: none;
}
.nav-container{
    width: 100%;
    background: $grey-color;
    position: fixed;
    z-index: 2;
}
.menu-wide ul { 
    list-style-type: none;
}
.menu-wide ul li {
    display: inline-block;
}
.menu-wide ul li a {
    display: block;
    color: $secondary-color;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 100;
    font-size: 1.0rem;
    padding: 15px 25px 15px 25px;
}
ul li a.active{
    color: $primary-color;
}
ul li a.active:hover{
    color: $primary-color;
}
.narrowLinks{
    display: none;
}

.wrap-body{
    max-width: 100%;
    margin: 0 auto;
    padding: 0 20px;
}
@media only screen and (min-width: 679px) {
    .navWide {
        max-width: 1240px;
        margin: 0 auto;
        display: grid;
        grid-template-columns: 1fr 2fr
    }
    .header {
        display: none;
    }
    .navNarrow {
        display: none;
    }
    .home-logo{
        justify-self: start;
    }
    .brand img{
        padding-left: 25px;
        width: 120px;
    }
    .menu-wide{
        justify-self: end;
        place-self:  center end;
    }

}
/*////////////////////   MENU MOBILE VERSION   ///////////////////*/
.hamburger {
    text-align: center;
    padding: 10px;
    margin-top: 10px;
}
.narrowLinks {
    padding-top: 10px;
}
.narrowLinks ul{
    width: 100%;
    margin: 0;
    padding: 0;
}
.narrowLinks ul li{
    list-style: none;
    text-align: center;
}
.narrowLinks ul li a{
    display: block;
    border-top: 1px solid $white-color;
    font-weight: bold;
    text-decoration: none;
    padding: 0.5em 15px;
    text-transform: uppercase;
    color: $secondary-color;
}
.narrowLinks ul li a:hover{
    color: $primary-color;
}
.narrowLinks ul li a.active{
    color: $primary-color;
}


.box{
    animation: anim 1s ease 0s 1;
}
@keyframes anim{
    0% {
        opacity: 0;

    }
    100% {
        opacity: 1;
    }
}
// CARROUSEL HOME//
.carousel_main{
    padding-top: 46px;
    @media only screen and (min-width: 679px) {
        padding-top: 74px;

    }
    
        .carrousel_wrapper {
        height:400px;
        overflow: hidden;
        .carrousel_image {
            
            background-size: cover !important;
            -webkit-background-size: cover;
            -moz-background-size: cover;
            -o-background-size: cover;
            background-size: cover;
            background-position: center !important;
        }    
    }

    .artist_name {
        position: absolute;
        top: 60%;
        left: 50%;
        width: 400px;
        height: 119px;
        -webkit-transform: translate(-50%, -50%);  
        transform: translate(-50%, -50%);   
        border: 2px solid white;
        .wrapper {
            border: 2px solid white;
            height: 80px;
            margin: 6px;
            text-align: center;
            color: #fff;
            font-size: 49px;
            padding: 21px 35px 0px 35px;
            text-transform: uppercase;
            animation: pulse 5s infinite;
            background-color: rgba( $white-color, 0.35);
        }
    }
}
  @keyframes pulse {
    0%, 100% {
      color: $primary-color;
    }
    50% {
      color: $tertiary-color;
    }
  }
// CSSTRANSITION //

.fade-appear{
    opacity: 0;
}
.fade-appear .fade-appear-active{
    opacity: 1;
    transition: opacity 600ms linear;
}

.slide-enter {
    transform: translateX(-100vw);
    opacity: 0;
    position: absolute;
    
}

/* Declare transition properties */
.slide-enter.slide-enter-active {
    transform: translateX(0);
    opacity: 1;
    transition: all 300ms linear 300ms;
}

/* EXIT TRANSITION */
.slide-exit {
    transform: translateX(0);
    opacity: 1;
    
}

.slide-exit.slide-exit-active {
    transform: translateX(100vw);
    opacity: 0;
    transition: all 300ms linear
}